main {
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
}

.container {
  position: relative;
  border: 1px solid;
  width: 80vw;
  height: 80vh;
  overflow: hidden;
}

.box {
  position: absolute;
  top: 0;
  left: 0;
  background: #0ac0d4;
  width: 50px;
  height: 50px;
  cursor: pointer;
}